import { useState } from "react"
import { Box, SxProps } from "@rp/react/web/mui/core"
import { Public } from "@rp/react/web/mui/icons"

type Props = {
  faviconUrl: string
  alt: string
  sx?: SxProps
}

const ExternalFavicon = ({ faviconUrl, alt, sx }: Props) => {
  const [imageState, setImageState] = useState<"loading" | "error" | "loaded">("loading")
  const showGenericIcon = imageState === "loading" || imageState === "error"

  return (
    <Box sx={sx}>
      {showGenericIcon ? <Public /> : null}
      <img
        src={faviconUrl}
        alt={alt}
        onLoad={() => setImageState("loaded")}
        onError={() => setImageState("error")}
        style={{
          display: showGenericIcon ? "none" : undefined,
          borderRadius: 2,
        }}
      />
    </Box>
  )
}
export { ExternalFavicon }
